<template>
    <div class="todoItem" v-bind:class="{'is-complete':todo.completed}">     <!--le v-bind:class= vient ajuster selon le boolen est vrai ou faux avec la classe CSS-->
        <div>
            <p>
                <input type="checkbox" v-on:change="surligne(todo.completed)" v-bind:checked="todo.completed">
                {{todo.title}}
            </p>
        </div>
        <div>
            <button @click="$emit('del-todo', todo.id)" class="del">x</button>
        </div>
    </div>
</template>

<script>
        export default {
        name: "TodoItem",
        props: ["todo"],
        data(){
            return{
                todos : this.todo
            }
        },
        methods: {
            surligne(isComplete) {
                this.todos.completed =! isComplete
            }
        }
        }
</script>

<style scoped>
    .todoItem {
        background: #ececec;
        padding: 10px;
        margin: 20px;
        border: none;
        font-family: 'Rancho', cursive;
        font-size: 1.5rem;
        border-radius: 10px;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        
    }


    .is-complete {
        text-decoration: line-through 2px solid red;
    }

    .del {
         background-image: linear-gradient(to bottom right, #ff4a64, #71244e);
        color: #fff;
        border: none;
        padding: 5px 9px;
        border-radius: 50%;
        cursor: pointer;
    }

    @media screen and (max-width: 768px) {
        .todoItem {
            height: 75px;
        }
    }
</style>