<template>
    <div>
        <!--Le premier todo on peut l'appeler comme on le veut, mais le second est celui que l'on prends dans l'export comme props. si on ne met pas de Key ca plante.-->
        <div v-bind:key="todo.id" v-for="todo in todos">
            <TodoItem v-bind:todo="todo" v-on:del-todo="$emit('del-todo', todo.id)" />
        </div>
    </div>    
</template>
<script>
import TodoItem from './TodoItem.vue';
export default {
    name: "Todos",
    components: {
        TodoItem
    },
    props:["todos"]
    
}
</script>


