<template>
  <div id="app">
    <Header />
    <AddTodo v-on:add-Todo="addTodo"/>
    <!--le "todos" (le second) c'est ce qui se trouve dans le data -->
    <Todos v-bind:todos="todos" v-on:del-todo="deleteTodo"/>
  </div>  
</template>

<script>
import Header from "./components/layout/Header";
import Todos from './components/Todos';
import AddTodo from './components/AddTodo';
//npm i axios pour aller chercher nos data JSONPlaceholder
import axios from 'axios';
export default {
  name: 'App',
  components: { /*Je viens ajouter ici mes components */
    Header,
    Todos,
    AddTodo
  },
  data() {
    return {
      todos: []
    }
  },
  methods: {
    // DELETE supprimer les tâches
    deleteTodo(id) {
      axios.delete(`https://jsonplaceholder.typicode.com/todos/${id}`)// pour effectuer le delete, il nous faut l'ID
        .then(res=>this.todos = this.todos.filter(todo => todo.id !== id,res.data))/*Doit générer un id pour ne plus avoir le bug de la suppression de tous les nouvelles tâches.*/
        .catch(err=>console.log(err));/*Pour lancer les erreurs*/
    },
    // ADD ajout de tâche
    addTodo(newTodo) {
      const { title, completed } = newTodo;
      axios.post('https://jsonplaceholder.typicode.com/todos', {
        title,
        completed
      })
        .then(res=>this.todos = [...this.todos, res.data]) /*résultat = CE todo = [spread operator Ce todo*/
        .catch(err=>console.log(err));/*Pour lancer les erreurs*/
    }
  },
  // GET aller chercher la liste de taches sur JSONPLACEHOLDER
  created() {
      axios.get('https://jsonplaceholder.typicode.com/todos?_limit=4')//le ?_limit=7 c<est pour limiter
      .then(res=>this.todos = res.data)
      .catch(err=>console.log(err));/*Pour lancer les erreurs*/
    }
}
</script>

