<template>
    <div>
        <form @submit="addTodo">
            <input class="textInput" type="text" v-model="title" name="title" placeholder="Ajouter une tâche" />
            <input class="submitButton" type="submit" value="Soumettre"/>
        </form>
    </div>    
</template>

<script>
//import { v4 as uuidv4 } from 'uuid';
export default {
    name: "AddTodo",
    date() {
        return {
            title: ''
        }
    },
    methods: {
        addTodo(e) {
            e.preventDefault();
            const newTodo = {
                //id: uuidv4(),
                title: this.title,
                completed: false
            }
            //Envoyer au parent
            this.$emit('add-todo', newTodo);
            this.title = '';
        }
    }
}
</script>

<style scoped>
    form {  
        display: flex;
        margin: 0 50px;
    }

    .textInput {    
        flex: 10;
        padding: 10px;
        border-radius: 10px;
        border: none;
        background-color: #ececec;
        font-family: 'Rancho', cursive;
        font-size: 1.5rem;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
    }

    .submitButton { 
        display: inline-block;
        border: none;
        border-radius: 10px;
        background-color: #d3f3cf;
        padding: 7px 10px;
        margin-left: 10px;
        cursor: pointer;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        font-family: 'Rancho', cursive;
        font-size: 1.7rem;
        flex: 2;
    }

    .submitButton:hover {   
        background-color: #bcf7b5;
    }

    @media screen and (max-width: 768px) {
        form {
            flex-direction: column;
        }
        .textInput {
            margin-bottom: 15px;
        }
        .submitButton {
            width: 100%;
            margin: 0 0 25px 0;
        }
    }
</style>


